export default {
  data() {
    return {
      query: "",
      articles: [],
    };
  },
  methods: {
    onClick(slug) {
      this.query = null;
      this.$router.push(slug);
    },
  },
  watch: {
    async query(query) {
      if (!query) {
        this.articles = [];
        return;
      }

      this.articles = await this.$content("blog")
        .only(["title", "path"])
        .sortBy("createdAt", "desc")
        .limit(12)
        .search(query)
        .fetch();
    },
  },
};
